import React from "react";
import { Card, CardContent, Typography, Button, Grid } from "@mui/material";

import user1 from "../../../assets/images/backgrounds/u5.jpg";
import user2 from "../../../assets/images/backgrounds/u1.jpg";
import user3 from "../../../assets/images/backgrounds/u4.jpg";

const blogs = [
  {
    img: user1,
    title: "Tu Clínica de Confianza",
    subtitle:
      "Bienvenido a tu Teleconsulta médica 1.",
    subtitle2:
      "23/09/2022 10:00 A.M.",
    btncolor: "secondary",
  },
  {
    img: user3,
    title: "Tu Clínica de Confianza",
    subtitle:
      "Bienvenido a tu Teleconsulta médica 2.",
    subtitle2:
      "23/09/2022 10:00 A.M.",
    btncolor: "secondary",
  },
  {
    img: user2,
    title: "Tu Clínica de Confianza",
    subtitle:
      "Bienvenido a tu Teleconsulta médica 3.",
    subtitle2:
      "23/09/2022 10:00 A.M.",
    btncolor: "secondary",
  },
];

const BlogCardConsultas = () => {
  return (
    <Grid container
    sx={{
      alignContent: "center",
      p: 0
    }}
    >
      {blogs.map((blog, index) => (
        <Grid
          key={index}
          item
          xs={12}
          lg={4}
          sx={{
            display: "flex",
            alignItems: "center",
          }}
        >
          <Card
            variant="outlined"
            sx={{
              alignItems: "center",
              p: 0,
              width: "100%",
            }}
          >
            <img src={blog.img} alt="img" width="100%" />
            <CardContent
              sx={{
                paddingLeft: "30px",
                paddingRight: "30px",
              }}
            >
              <Typography
                sx={{
                  fontSize: "h4.fontSize",
                  fontWeight: "500",
                }}
              >
                {blog.title}
              </Typography>
              <Typography
                color="textSecondary"
                sx={{
                  fontSize: "14px",
                  fontWeight: "400",
                  mt: 1,
                }}
              >
                {blog.subtitle}
              </Typography>
              <Typography
                color="textSecondary"
                sx={{
                  fontSize: "14px",
                  fontWeight: "400",
                  mt: 1,
                }}
              >
                {blog.subtitle2}
              </Typography>
              <Button
                variant="contained"
                sx={{
                  alignItems:"center",
                  mt: "15px",
                }}
                color={blog.btncolor}
              >
                Ingresar
              </Button>
            </CardContent>
          </Card>
        </Grid>
      ))}
    </Grid>
  );
};

export default BlogCardConsultas;
